(function( window, document, angular, undefined ){'use strict';

angular.module( 'app', ['brk'] )

.controller('BUNS', [ 'brkMenu', function( brkMenu ){


	var nav = [
		'prime', 'notify', 'me', 'you'
	];
	var youNav = [
		'nav', 'games', 'favourites'
	];
	var i;
	var buns = this;
	var bunsYou;
	var bunsMe;
	var bunsPrime;
	var bunsNotify;


	buns.nav = brkMenu;
	buns.activeYou;
	buns.toggleMenu = function( item ) {
		item.open();

		if ( !bunsYou.is.open ) {
			bunsYou.open();
		}
		buns.activeYou = item.name;
	}



	for ( i = 0; i < nav.length; i+=1 ) {
		brkMenu.add({
			name: nav[i]
		});
	}

	for ( i = 0; i < youNav.length; i+=1 ) {
		brkMenu.get( 'you' ).add({
			name: youNav[i],
		});
	}


	//	These could get used a lot
	//		so it's probably worth memoing them
	bunsYou =		brkMenu.get( 'you' );
	bunsMe =		brkMenu.get( 'me' );
	bunsPrime =		brkMenu.get( 'prime' );
	bunsNotify =	brkMenu.get( 'notify' );
	//	And scoping them
	buns.you = bunsYou;
	buns.me = bunsMe;
	buns.prime = bunsPrime;
	buns.notify = bunsNotify;

	brkMenu.linkMenus([
			brkMenu.get( 'you/nav' ),
			brkMenu.get( 'you/games' ),
			brkMenu.get( 'you/favourites' )
		], 'exactly one is', 'open' );

	brkMenu.linkMenus([
			brkMenu.get( 'me' ),
			brkMenu.get( 'you' )
		], 'one is', 'open' );

	brkMenu.get( 'notify' ).open();


}]);

})( window, document, angular );